let baseUrl = ''

console.log(process.env.NODE_ENV)

if (process.env.NODE_ENV === 'development') {
  baseUrl = 'http://localhost:8888'
} else if (process.env.NODE_ENV === 'production') {
  baseUrl = 'https://verify.cloudcrowd.com.cn'
}

export {
  baseUrl
}



